$flaticon-font: "flaticon";

@font-face {
    font-family: $flaticon-font;
    src: url("flaticon.ttf?9272dd6433cc5d8496622c01d97e5324") format("truetype"),
url("flaticon.woff?9272dd6433cc5d8496622c01d97e5324") format("woff"),
