url("flaticon.eot?9272dd6433cc5d8496622c01d97e5324#iefix") format("embedded-opentype"),
url("flaticon.svg?9272dd6433cc5d8496622c01d97e5324#flaticon") format("svg");
}

i[class^="flaticon-"]:before, i[class*=" flaticon-"]:before {
    font-family: flaticon !important;
    font-style: normal;
    font-weight: normal !important;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

